ul{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
}

li {
    padding: 10px;
    width: 250px;
    max-width: 250px;
    max-height: 400px;
    list-style: none;
}


.card {
   padding: 1rem;
    height: 100%;
    width: 100%;
    box-shadow: 10px 10px 25px #ccc;
    align-items: center;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

.card  > img {
    width: 100%;
    height: 50%;
}

.card h3 {
    margin: 0;
    padding: 5px;
}

.card p {
    margin: 0;
    padding: 3px;
}

.card h2 {
     margin: 0;

}

.card article {
    padding: 5px;

}


.card h3{
    padding: 4px;
}

.card p {
    padding: 3px;
}

